
// home

	

	body{

      font-family: 'open-regular';
		strong{
         font-family: 'open-bold';
      }
	}


   .container{
   	width: 1280px;
   	margin: 0 auto;
   }

   header{


      h1{
         background: url('../images/logo-rapido.png') center center no-repeat;
         display: inline-block;
         vertical-align: middle;
         width: 200px;
         height: 110px;
         font-size: 0px;
      }

      .movile{

         text-align: right;
         padding: 30px 0px;

         p{
            display: inline-block;
            vertical-align: middle;
            font-size: 12px;
            color: #000000;
         }

         img{
            display: inline-block;
            vertical-align: middle;
            padding-left: 10px;
         }
      }


   }




   section{

      
      .show-mobile{
         display: none;
      }

      .banner-bg{
         // background: url('../images/banner.jpg') right center no-repeat;

         position: relative;
         background-color: #fbe4cb;
         display: inline-block;
         vertical-align: middle;
         width: 100%;
         // height: 500px;

         picture{
            float: right;
         }

         .banner-text{
            position: absolute;
            margin-top: 50px;
            top: 0;

            strong{
               color: #36464d;
               font-size: 40px;
               font-family: 'open-extrabold';

               span{
                  color: #fa6c10;
               }
            }

            p{

               padding: 25px 0px;
               color: #36464d;
               font-size: 28px;
            }

            .button-banner{

               margin: 20px 0px 35px;
               a{

                  color: #ffffff;
                  font-size: 30px;
                  font-family: 'open-extrabold';
                  padding: 9px 10px;
                  background-color: #fa6c10;

                  &:hover{
                     text-decoration: none;
                  }

               }
            }


            .and{
               margin-top: 20px;
               font-size: 14px;

               strong{
                  font-size: 14px;
               }

               i.android-icon{
                  background: url('../images/android.png') center center no-repeat;
                  display: inline-block;
                  vertical-align: middle;
                  width: 50px;
                  height: 50px;
               }

               i.apple-icon{
                  background: url('../images/apple.png') center center no-repeat;
                  display: inline-block;
                  vertical-align: middle;
                  width: 30px;
                  height: 30px;
               }


            }

         }




      }

      
      


      .banner2-bg{
         background: linear-gradient(#ff9000, #ff4b0e);
         position: relative;

         picture{
            float: right;
         }

         .banner-text{
            position: absolute;
            top: 0px;


            h3{
               margin-top: 20px;
               font-size: 40px;
               font-family: 'open-extrabold';
               color: #ffffff;
               
            }


            .banner-logos div{
               text-align: center;

               img{
                  display: inline-block;
                  margin: 20px 0px 10px;
               }
            }

            p{

               padding: 25px 0px;
               color: #36464d;
               font-size: 28px;
            }

            .button-banner{

               display: inline-block;
               margin-top: 40px;

               a{

                  color: #fa6c10;
                  font-size: 30px;
                  font-family: 'open-extrabold';
                  padding: 9px 10px;
                  background-color: #ffea00;

                  &:hover{
                     text-decoration: none;
                  }

               }
            }


            .and{
               margin-top: 40px;
               font-size: 14px;
               color: #ffffff;

               strong{
                  font-size: 14px;
               }

              i.android-icon{
                  background: url('../images/android2.png') center center no-repeat;
                  display: inline-block;
                  vertical-align: middle;
                  width: 50px;
                  height: 50px;
               }

               i.apple-icon{
                  background: url('../images/apple2.png') center center no-repeat;
                  display: inline-block;
                  vertical-align: middle;
                  width: 30px;
                  height: 30px;
               }
            }

         }

      }



      // change arrows

      
      .slick-prev{
         background: url('../images/left.png') center center no-repeat;
         width: 30px;
         height: 30px;
         border: 0px;
         box-sizing: inherit;
         padding: 0px;
         position: absolute;
         z-index: 999;
         font-size: 0px;
         top: 50%;
         left: 3%;

         &:focus{
            padding: 0px;
            border: 0px;
            box-sizing: inherit;
            outline: inherit;
         }

      }

      .slick-next{
         background: url('../images/right.png') center center no-repeat;
         width: 30px;
         height: 30px;
         border: 0px;
         box-sizing: inherit;
         padding: 0px;
         position: absolute;
         z-index: 999;
         font-size: 0px;
         top: 50%;
         right: 3%;

         &:focus{
            padding: 0px;
            border: 0px;
            box-sizing: inherit;
            outline: inherit;
         }

      }
   }






   .video{

      .vide-text {
        padding: 130px 10px;

        strong{
           font-weight: bold;
           font-size: 43px;
           color: #fa6c10;
           margin: 10px 0px;
           display: inline-block;
         }

         p{
           strong{
            font-size: 19px;
            color: #fa6c10;
            text-transform: uppercase;
            line-height: 25px;
           }
         }
         span{
               font-size: 16px;
               color: #36464d;
            }
      }


   }


